<h1 id="日志" tabindex="-1"><a class="header-anchor" href="#日志" aria-hidden="true">#</a> 日志</h1>
<p>如果没有在配置中指定日志选项，将不会记录任何日志。仅在配置了相关日志策略时启用，配置详细请参考：<RouterLink to="/6.x/common/config.html">配置</RouterLink></p>
<h2 id="日志配置" tabindex="-1"><a class="header-anchor" href="#日志配置" aria-hidden="true">#</a> 日志配置</h2>
<p>你可以配置多个日志的 <code>channel</code>，每个 <code>channel</code> 里的 <code>driver</code> 对应不同的日志驱动，内置可用的 <code>driver</code> 如下表：</p>
<table>
<thead>
<tr>
<th>名称</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td><code>stack</code></td>
<td>复合型，可以包含下面多种驱动的混合模式</td>
</tr>
<tr>
<td><code>single</code></td>
<td>基于 <code>StreamHandler</code> 的单一文件日志，参数有 <code>path</code>，<code>level</code></td>
</tr>
<tr>
<td><code>daily</code></td>
<td>基于 <code>RotatingFileHandler</code> 按日期生成日志文件，参数有 <code>path</code>，<code>level</code>，<code>days</code>(默认 7 天)</td>
</tr>
<tr>
<td><code>slack</code></td>
<td>基于 <code>SlackWebhookHandler</code> 的 Slack 组件，参数请参考源码：<a href="https://github.com/w7corp/wechat/blob/master/src/Kernel/Log/LogManager.php#L247" target="_blank" rel="noopener noreferrer">LogManager.php<OutboundLink/></a></td>
</tr>
<tr>
<td><code>syslog</code></td>
<td>基于 <code>SyslogHandler</code> Monolog 驱动，参数有 <code>facility</code> 默认为 <code>LOG_USER</code>，<code>level</code></td>
</tr>
<tr>
<td><code>errorlog</code></td>
<td>记录日志到系统错误日志，基于 <code>ErrorLogHandler</code>，参数有 <code>type</code>，默认为 <code>ErrorLogHandler::OPERATING_SYSTEM</code></td>
</tr>
</tbody>
</table>
<h3 id="自定义日志驱动" tabindex="-1"><a class="header-anchor" href="#自定义日志驱动" aria-hidden="true">#</a> 自定义日志驱动</h3>
<p>由于日志使用的是 <a href="https://github.com/Seldaek/monolog" target="_blank" rel="noopener noreferrer">Monolog<OutboundLink/></a>，所以，除了默认的文件式日志外，你可以自定义日志处理器：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">Monolog<span class="token punctuation">\</span>Logger</span><span class="token punctuation">;</span>
<span class="token keyword">use</span> <span class="token package">Monolog<span class="token punctuation">\</span>Handler<span class="token punctuation">\</span>RotatingFileHandler</span><span class="token punctuation">;</span>


<span class="token comment">// 注册自定义日志</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getLogger</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">extend</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'mylog'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token variable">$app</span><span class="token punctuation">,</span> <span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">return</span> <span class="token keyword">new</span> <span class="token class-name">Logger</span><span class="token punctuation">(</span><span class="token variable">$this</span><span class="token operator">-></span><span class="token function">parseChannel</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">,</span> <span class="token punctuation">[</span>
        <span class="token variable">$this</span><span class="token operator">-></span><span class="token function">prepareHandler</span><span class="token punctuation">(</span><span class="token keyword">new</span> <span class="token class-name">RotatingFileHandler</span><span class="token punctuation">(</span>
            <span class="token variable">$config</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'path'</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token variable">$config</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'days'</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token variable">$this</span><span class="token operator">-></span><span class="token function">level</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span>
        <span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div><blockquote>
<p>{info} 在你自定义的闭包函数中，可以使用 <code>EasyWeChat\Kernel\Log\LogManager</code> 中的方法，具体请查看 SDK 源代码。</p>
</blockquote>
<p>配置文件中在 <code>driver</code> 部分即可使用你自定义的驱动了：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token string single-quoted-string">'logging'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'default'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'dev'</span><span class="token punctuation">,</span> <span class="token comment">// 默认使用的 channel，生产环境可以改为下面的 prod</span>
    <span class="token string single-quoted-string">'channels'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token comment">// 测试环境</span>
        <span class="token string single-quoted-string">'dev'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token string single-quoted-string">'driver'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'mylog'</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'path'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'/tmp/easywechat.log'</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'level'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'debug'</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'days'</span> <span class="token operator">=></span> <span class="token number">5</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>

        <span class="token comment">//...</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">,</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div>